    border-radius: 25px;
}
div.largecontent {
    margin-top: 1em;
    margin-left: auto;
    margin-right: auto;
    width: 25em;
    height: fit-content;
    vertical-align: middle;
    text-align: center;
    flex: auto;
    background-color: white;
    /* box-shadow: 0 10px 29px 0 gray; */
